.button {
    border-radius: 0.2em 1.2em 0.2em 1.2em;
    width: min-content;
    height: 6em;
    border: none;
    background-image: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
    
}

.order__button-text {
    font-family: 'Comfortaa', cursive;
    font-weight: 900;
    font-size: 1.5em;
    padding: 1em 2em;
    color: @purple;
}

.button:hover {
    background-image: radial-gradient(circle, rgba(148,187,233,1) 0%, rgba(238,174,202,1) 100%);
    box-shadow: 5px 5px 10px 0px rgba(26, 27, 25, 0.3)
}