.footer {
    // background: linear-gradient(180deg, rgba(2, 0, 36, 0.8547794117647058) 0%, rgba(70, 52, 78, 1) 100%);
    background-image: url(/source/img/background_flowers.webp);
    padding: 20px;
}

.footer a {
    color: @light-yellow;
    text-decoration: none;
    font-size: 1.2em;
}

.footer > .wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    @media (min-width: @tablet-width) {
        flex-direction: row;
    justify-content: space-between;
    }
}

.phone {
    display: block;
}
.phone:hover {
    color: @yellow;
}

.social__list {
    list-style-type: none;
    text-align: center;

    @media (min-width: @tablet-width) {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}

.social__item {
    margin: 10px;
    padding: 10px;
}

.social__link:hover {
    color: @yellow;
}