@import url('normalize.css');
* {
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: 'Comfortaa', cursive;
  src: url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;700&display=swap');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Comfortaa', cursive;
  src: url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;700&display=swap');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page {
  font-family: 'Comfortaa', cursive;
  font-size: 20px;
  line-height: 1.5em;
  color: #ffffe0;
  background-color: #46344e;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.gradientText {
  background-image: linear-gradient(to right, #ff6a00, #f7fa3b, #e3b3c9);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.header {
  background: linear-gradient(180deg, rgba(2, 0, 36, 0.85477941) 0%, #46344e 100%);
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .header > .wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.logo {
  display: none;
  transition: transform 0.5s ease;
}
@media (min-width: 768px) {
  .logo {
    display: inline-block;
  }
}
.logo:hover {
  transform: scale(1.03);
}
.navigation-list {
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}
@media (min-width: 768px) {
  .navigation-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 1.5em 0;
  }
}
.header-nav__item {
  margin-top: 1em;
  margin-bottom: 1em;
}
@media (min-width: 768px) {
  .header-nav__item {
    margin-top: 0;
  }
}
.header-nav__link {
  font-size: 1.5em;
  margin-bottom: 1em;
  color: #ffffe0;
  text-decoration: none;
  margin-right: 1.5em;
}
@media (min-width: 768px) {
  .header-nav__link {
    font-size: 1em;
  }
}
.header-nav__link:hover {
  color: #faed26;
}
.header-nav__link:active {
  text-decoration: solid;
}
.header-nav__link:focus {
  outline-color: #faed26;
}
.wrapper {
  padding: 0 20px;
  margin: auto;
}
@media (min-width: 768px) {
  .wrapper {
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    padding: 0;
    max-width: 1220px;
  }
}
.section {
  margin-bottom: 30px;
}
.section__title {
  font-size: 1.8em;
  line-height: 2em;
  font-weight: 700;
  background-image: linear-gradient(to right, #ff6a00, #f7fa3b, #e3b3c9);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.section__text {
  font-style: 1em;
  line-height: 1.5em;
}
.main__heading {
  width: 100%;
  min-height: 100%;
  background-image: url('../img/bg_round.webp');
  background-size: cover;
  background-position: center;
  margin-top: -50px;
}
@media (min-width: 768px) {
  .main__heading {
    min-height: 380px;
  }
}
.main-title {
  padding-top: 1em;
  text-align: center;
  line-height: 1.2em;
  text-shadow: 5px 5px 4px rgba(0, 0, 0, 0.5);
}
@media (min-width: 768px) {
  .main-title {
    padding-top: 5em;
  }
}
.main-description {
  text-align: end;
  padding-bottom: 1.4em;
  text-shadow: 5px 5px 4px rgba(0, 0, 0, 0.5);
}
.page-main {
  background: radial-gradient(circle, #b8b4ae 0%, #46344e 100%);
  padding-bottom: 30px;
}
.products {
  background-image: url('./img/icons/flowers.svg');
  background-size: contain;
}
.products__list {
  list-style-type: none;
  display: grid;
  justify-items: center;
  gap: 40px;
  margin: auto;
  grid-template-columns: 1fr;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .products__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
.products__item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  max-width: 480px;
  background-color: #9d8d8f;
  border-radius: 10px 70px 10px 70px;
  padding: 2em;
}
.products__item-image {
  grid-area: 1 / 1 / 2 / -1;
  align-self: center;
  max-width: inherit;
}
.product__image {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 10px 70px 10px 70px;
}
.products__item-title {
  grid-area: 2 / 1 / 3 / -1;
  color: #faed26;
  font-weight: 700;
  font-size: 1.5em;
  line-height: 1em;
  margin: 1em;
  text-align: center;
}
@media (min-width: 768px) {
  .products__item-title {
    font-size: 2em;
    line-height: 1.5em;
  }
}
.products__item-description {
  grid-area: 3 / 1 / 4 / -1;
  margin-bottom: 10px;
}
.products__item-price {
  grid-area: 4 / 1 / 5 / 2;
  color: #faed26;
  font-weight: 700;
  font-size: 1.5em;
  line-height: 1em;
  align-self: center;
  text-align: center;
  width: max-content;
}
@media (min-width: 768px) {
  .products__item-price {
    font-size: 2em;
    line-height: 1.5em;
  }
}
.button {
  grid-area: 4 / 2 / 5 / 3;
}
@media (max-width: 767px) {
  .button {
    max-height: 1.5em;
    align-self: center;
  }
}
.button {
  border-radius: 0.2em 1.2em 0.2em 1.2em;
  width: min-content;
  height: 6em;
  border: none;
  background-image: radial-gradient(circle, #eeaeca 0%, #94bbe9 100%);
}
.order__button-text {
  font-family: 'Comfortaa', cursive;
  font-weight: 900;
  font-size: 1.5em;
  padding: 1em 2em;
  color: #46344e;
}
.button:hover {
  background-image: radial-gradient(circle, #94bbe9 0%, #eeaeca 100%);
  box-shadow: 5px 5px 10px 0px rgba(26, 27, 25, 0.3);
}
.footer {
  background-image: url(/source/img/background_flowers.webp);
  padding: 20px;
}
.footer a {
  color: #ffffe0;
  text-decoration: none;
  font-size: 1.2em;
}
.footer > .wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .footer > .wrapper {
    flex-direction: row;
    justify-content: space-between;
  }
}
.phone {
  display: block;
}
.phone:hover {
  color: #faed26;
}
.social__list {
  list-style-type: none;
  text-align: center;
}
@media (min-width: 768px) {
  .social__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.social__item {
  margin: 10px;
  padding: 10px;
}
.social__link:hover {
  color: #faed26;
}
