.products {
    background-image: url('./img/icons/flowers.svg');
    background-size: contain;
}

.products__list {
    list-style-type: none;
    display: grid;
    justify-items: center;
    gap: 40px;
    margin: auto;
    grid-template-columns: 1fr;
    margin-bottom: 30px;

    @media (min-width: @tablet-width) {
    grid-template-columns: repeat(2, 1fr);
    }
}

.products__item {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    max-width: 480px;
    background-color: @lightpurple;
    border-radius: 10px 70px 10px 70px;
    padding: 2em;
}

.products__item-image {
    grid-area: 1 / 1 / 2 / -1;
    align-self: center;
    max-width: inherit;
}

.product__image {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 10px 70px 10px 70px;
}

.products__item-title {
    grid-area: 2 / 1 / 3 / -1;
    color: @yellow;
    font-weight: 700;
    font-size: 1.5em;
    line-height: 1em;
    margin: 1em;
    text-align: center;
    
    @media (min-width: @tablet-width) {
        font-size: 2em;
        line-height: 1.5em;
    }
}
.products__item-description {
    grid-area: 3 / 1 / 4 / -1;
    margin-bottom: 10px;
}

.products__item-price {
    grid-area: 4 / 1 / 5 / 2;
    color: @yellow;
    font-weight: 700;
    font-size: 1.5em;
    line-height: 1em;
    align-self: center;
    text-align: center;
    width: max-content;
    
    @media (min-width: @tablet-width) {
        font-size: 2em;
        line-height: 1.5em;
    }
}

.button {
    grid-area: 4 / 2 / 5 / 3;

    @media(max-width: @mobile-width) {
        max-height: 1.5em;
        align-self: center;
    }
}