

.main__heading {
    width: 100%;
    min-height: 100%;
    background-image: url('../img/bg_round.webp');
    background-size: cover;
    background-position: center;
    margin-top: -50px;

    @media (min-width: @tablet-width) {
        min-height: 380px;
    }
}

.main-title {
    padding-top: 1em;
    text-align: center;
    line-height: 1.2em;
    text-shadow: 5px 5px 4px rgba(0, 0, 0, 0.5);
    
    @media (min-width: @tablet-width) {
        padding-top: 5em;
    }
}

.main-description {
    text-align: end;
    padding-bottom: 1.4em;
    text-shadow: 5px 5px 4px rgba(0, 0, 0, 0.5);
}
