.header {
    background: linear-gradient(180deg, rgba(2,0,36,0.8547794117647058) 0%, rgba(70,52,78,1) 100%);

    margin-bottom: 30px;
}
.header > .wrapper {
    @media (min-width: @tablet-width) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}
.logo {
    display: none;
    transition: transform 0.5s ease;
    
    @media (min-width: @tablet-width) {
        display: inline-block;
    }
}

.logo:hover {
    transform: scale(1.03);
}
.navigation-list {
    list-style-type: none;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;

    @media (min-width: @tablet-width) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        padding: 1.5em 0;
    }
}

.header-nav__item {
    margin-top: 1em;
    margin-bottom: 1em;
    @media (min-width: @tablet-width) {
        margin-top: 0;
    }
}
.header-nav__link {
    font-size: 1.5em;
    margin-bottom: 1em;
    color: @light-yellow;
    text-decoration: none;
    margin-right: 1.5em;

    @media (min-width: @tablet-width) {
        font-size: 1em;
    }
}

.header-nav__link:hover {   
    color: @yellow;
}

.header-nav__link:active {   
    text-decoration: solid;
}

.header-nav__link:focus {
    outline-color: @yellow;
}