* {
    margin: 0;
    padding: 0;
}

@font-face {
    font-family: 'Comfortaa', cursive;
    src: url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;700&display=swap');
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Comfortaa', cursive;
    src: url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;700&display=swap');
    font-weight: 700;
    font-style: normal;
    font-display: swap;
}

.page {
    font-family: 'Comfortaa', cursive;
    font-size: 20px;
    line-height: 1.5em;
    color: @light-yellow;
    background-color: #46344e;

}


.visually-hidden {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    clip: rect(0 0 0 0);
    overflow: hidden;
}